import React from 'react'
import { useParams, useRouteMatch } from 'react-router-dom'
//import Covid_Page from './covid-page.js'
//import { usePagination } from '@material-ui/lab/Pagination';
import * as bs from 'react-bootstrap'
import Covid_Card from './covid-card.js'
import AppContext from './context.js'
import { MDBCol, MDBIcon } from "mdbreact"

function Covid_Home(props) {

  const state = React.useContext(AppContext)
  const [initial, setInitial] = React.useState(0)

  const filteredCampaigns = []
  const pageCampaigns = []
  //let numPages = 0

  // Route match
  console.log("state.campaigns 1: ", state.campaigns)

  const match_category = useRouteMatch("/campaign-category/:name")
  const match_goal = useParams("/campaign-goal/minGoal/:min/maxGoal/:max")
  //const match_hearts = useParams("/campaign-hearts/:name")
  console.log("match_goal: ", match_goal)
  console.log("state.campaigns 2: ", state.campaigns)

  if (match_category != null) {
    for (const p of state.campaigns) {
      if (p.category === (match_category.params.name)) {
        filteredCampaigns.push(p)
      }
    }
  }
  else if (match_goal.min != null && match_goal.max != null) {
    console.log("state.campaigns 3: ", state.campaigns)
    const { min, max } = match_goal

    console.log("min: ", min)
    console.log("max: ", max)
    
    for (const p of state.campaigns) {
      if (p.goal >= min && p.goal <= max) {
        console.log("p.title: ", p.title)
        console.log("p.goal: ", p.goal)
        filteredCampaigns.push(p)
      }
    }

    console.log("state.campaigns 4: ", state.campaigns)

  }
  else {
    for (const p of state.campaigns) {
      filteredCampaigns.push(p)
    }
  }
  // else if (match_hearts != null) {
  //   for (const p of state.campaigns) {
  //     if (p.hearts === (match.params.name)) {
  //       filteredCampaigns.push(p)
  //     }
  //   }
  // }
  



  // Pagination

  if(filteredCampaigns.length <= 18){
    for (let i = 0; i < filteredCampaigns.length; i++){
      pageCampaigns.push(filteredCampaigns[i])
    }
    //setInitial(0)
  }
  else{

    //numPages = Math.ceil(filteredCampaigns.length / 18)

    if(initial < 0){
      setInitial(0)
    }
    if(initial > filteredCampaigns.length - 18){
      setInitial(initial-18)
    }

    for (let i = initial; i < initial + 18; i++){
      pageCampaigns.push(filteredCampaigns[i])
    }
  }

  return (
        //<Covid_Page filteredCampaigns={pageCampaigns}/> 

        <div>
            <MDBCol md="12">
                <form className="form-inline mt-4 mb-4">
                    <MDBIcon icon="search" />
                    <input className="form-control form-control-sm ml-3 w-75" type="text" placeholder="Search" aria-label="Search" />
                </form>
            </MDBCol>

            <bs.Pagination className="float-right">
              <bs.Pagination.Prev
                onClick={e => {
                  setInitial(initial-18)
                }}
              >
                Previous Page
              </bs.Pagination.Prev>
              <bs.Pagination.Next
                onClick={e => {
                  setInitial(initial+18)
                }}
              >
                Next Page
              </bs.Pagination.Next>
            </bs.Pagination>

            <bs.Container>
                <bs.CardColumns>
                    {pageCampaigns.map((campaign, idx) => {
                        return (
                            <Covid_Card key={ImageBitmapRenderingContext} campaign={campaign} />
                        )
                    })}
                </bs.CardColumns>
            </bs.Container>
        </div>
   );
}

export default Covid_Home;