import React from 'react'
import * as bs from 'react-bootstrap'
import { Link } from "react-router-dom"
import AppContext from './context.js'


function LeftContainer(props) {

    const state = React.useContext(AppContext)
    const [min, setMin] = React.useState(0)
    const [max, setMax] = React.useState(0)

    const categories = {}
    let total = 0

    for (const p of state.campaigns) {
        if (p.category in categories) {
            categories[p.category] = categories[p.category] + 1
            total += 1
        }
        else {
            categories[p.category] = 1
            total += 1
        }
    }

    return (
        <bs.Nav className="flex-column">
            <Link to="/covid-home/" className="nav-link">
                All Categories ({total})
            </Link>
            {Object.keys(categories).map((name, idx) => {
                return <Link
                    key={idx}
                    to={"/campaign-category/" + name}
                    className="nav-link"
                >
                    <i>{name} ({categories[name]})</i>
                </Link>
            })}
            <p>Min</p>

            <input onChange={event =>
                setMin(event.target.value)}
            />
            <p>Max</p>
            <input onChange={event =>
                setMax(event.target.value)}
            />
            <Link 
            to={"/campaign-goal/minGoal/" + min + "/maxGoal/" + max}
            className="nav-link"
            >
                Goal
            </Link>
            {/* <bs.Form>
                <bs.Form.Group>
                    <bs.Form.Label>Min:</bs.Form.Label>
                    <Form.Control type="text" label="Min" />
                </bs.Form.Group>
                <bs.Form.Group>
                    <bs.Form.Label>Max:</bs.Form.Label>
                    <Form.Control type="text" label="Max" />
                </bs.Form.Group>
                <bs.Button variant="primary" type="submit"></bs.Button>          
            </bs.Form> */}
        </bs.Nav>
    )
}


export default LeftContainer